import { ModalProvider, useModal } from './context/ModalContext';

export default function RootLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  return (
    <html lang="en">
      <body>
        <ModalProvider>
          <LayoutContent>{children}</LayoutContent>
        </ModalProvider>
      </body>
    </html>
  );
}

function LayoutContent({ children }: { children: React.ReactNode }) {
  const { isQuoteOpen, openQuote, closeQuote } = useModal();

  return (
    <html lang="en">
      <body>
        <nav className="nav">
          <div className="container nav-content">
            <div className="logo"><a href="/" style={{ textDecoration: 'none', color: 'inherit' }}>CallNest</a></div>
            <ul className="nav-links">
              <li><a href="/#features">Product</a></li>
              <li><a href="/pricing">Pricing</a></li>
              <li><a href="/solutions">Solutions</a></li>
            </ul>
            <button className="btn btn-primary" onClick={() => setIsQuoteOpen(true)}>Get Started</button>
          </div>
        </nav>

        {children}

        <footer className="section" style={{ padding: '4rem 0', borderTop: '1px solid var(--border)' }}>
          <div className="container">
            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
              <div className="logo">CallNest</div>
              <div style={{ color: 'var(--text-muted)', fontSize: '0.85rem' }}>
                Â© 2025 CallNest AI Inc. All rights reserved.
              </div>
              <div className="nav-links">
                <a href="#">Privacy</a>
                <a href="#">Terms</a>
                <a href="#">Twitter</a>
              </div>
            </div>
          </div>
        </footer>

        {/* Global Quote Modal */}
        {isQuoteOpen && (
          <div className="modal-overlay">
            <div className="modal-content glass-card" style={{ maxWidth: '800px' }}>
              <button className="close-btn" style={{ position: 'absolute', top: '1rem', right: '1rem', background: 'none', border: 'none', color: 'white', fontSize: '1.5rem', cursor: 'pointer' }} onClick={() => setIsQuoteOpen(false)}>&times;</button>
              <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '4rem' }}>
                <div>
                  <h2 style={{ fontSize: '2rem', marginBottom: '1rem' }}>Modernize your workflow.</h2>
                  <p style={{ color: 'var(--text-secondary)' }}>Join the waitlist for our Enterprise Voice API. Our team will reach out within 24 hours.</p>
                </div>
                <form onSubmit={async (e) => {
                  e.preventDefault();
                  const target = e.target as any;
                  const data = {
                    name: target[0].value,
                    email: target[1].value,
                    message: "Quote Request from SaaS Landing Page"
                  };
                  try {
                    const res = await fetch('http://localhost:3001/api/leads', {
                      method: 'POST',
                      headers: { 'Content-Type': 'application/json' },
                      body: JSON.stringify(data)
                    });
                    if (res.ok) {
                      alert('Your request has been logged. Our team will contact you soon.');
                      setIsQuoteOpen(false);
                    } else {
                      alert('Failed to submit request. Please try again.');
                    }
                  } catch (err) {
                    console.error(err);
                    alert('Server error. Please ensure the backend is running.');
                  }
                }}>
                  <div className="form-group" style={{ marginBottom: '1rem' }}>
                    <label style={{ display: 'block', marginBottom: '0.5rem' }}>Full Name</label>
                    <input type="text" name="name" className="form-input" placeholder="e.g. Amaan Kamil" required style={{ width: '100%', padding: '0.75rem', borderRadius: '0.5rem', background: '#0f172a', border: '1px solid #334155', color: 'white' }} />
                  </div>
                  <div className="form-group" style={{ marginBottom: '1rem' }}>
                    <label style={{ display: 'block', marginBottom: '0.5rem' }}>Contact Email</label>
                    <input type="email" name="email" className="form-input" placeholder="you@company.com" required style={{ width: '100%', padding: '0.75rem', borderRadius: '0.5rem', background: '#0f172a', border: '1px solid #334155', color: 'white' }} />
                  </div>
                  <button type="submit" className="btn btn-primary" style={{ width: '100%', marginTop: '1rem' }}>Submit Request</button>
                </form>
              </div>
            </div>
          </div>
        )}
      </body>
    </html>
  );
}
